<template>
    <v-container class="d-flex justify-center mt-15">  

        <form @submit.prevent="registar" enctype="text/plain">
        <v-card :loading="loading" :disabled="loading" height="auto" max-width="600" outlined>
            <v-card-title class="text-h3" primary-title>
                Cadastrar-se
            </v-card-title>
            <v-card-text>
                <v-row g-2>

                    <v-col cols="12" sm="6">
                        <v-text-field 
                        type="text"
                        :rules="nomeRegra"
                        required
                        v-model="form.nome"
                        label="Nome usuário"
                        prepend-icon="mdi-account"
                        hint="Informe o usuário">
                        </v-text-field>
                    </v-col>

                    <v-col cols="12" sm="6">
                        <v-text-field 
                        type="password"
                        :rules="passwordRegra"
                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show1 ? 'text' : 'password'"
                        v-model="form.senha"
                        required
                        @click:append="show1 = !show1"
                        label="Palavra-passe"
                        prepend-icon="mdi-lock"
                        hint="Informe a Palavra-passe">
                        </v-text-field>                        
                    </v-col>

                    <v-col cols="12" sm="6">
                        <v-text-field 
                        type="password"
                        :rules="passwordRegra"
                        :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show2 ? 'text' : 'password'"
                        v-model="senhaPreview"
                        @click:append="show2 = !show2"
                        label="Palavra-passe novamente"
                        prepend-icon="mdi-lock"
                        hint="Informe a Palavra-passe">
                        </v-text-field>                        
                    </v-col>

                    <v-col cols="12" sm="6">
                        
                        
                        <v-checkbox 
                        v-model="form.concorda"
                        label="Concorda com os nossos termos e condições" indeterminate ></v-checkbox>
                    </v-col>

                </v-row>
                    

            </v-card-text>
            <v-card-actions>
                <v-btn color="success" type="submit" > registar </v-btn>
                <v-spacer></v-spacer>
                <v-btn :to="{ name:'entrada' }" color="primary"> entrada </v-btn>
                
            </v-card-actions>                
        </v-card> 
        </form>
        <v-snackbar
        v-model="snackbar"
      >
        {{ text }}
  
        <template v-slot:action="{ attrs }">
          <v-btn
            color="pink"
            text
            v-bind="attrs"
            @click="snackbar = false"
          >
            fechar
          </v-btn>
        </template>
      </v-snackbar>
</v-container>
  </template>
  
  <script>
    import { mapActions, mapGetters } from 'vuex'
  export default {
    data(){
        return {
            form:{
                nome:'',
                senha:'',
                concorda:false
            },
            snackbar: false,
            text: `Novo usuário registrado com sucesso!`,
            loading:false,
            senhaPreview:'',
            show1:false,
            show2:false,
            nomeRegra: [
                    v => !!v || 'Informe o nome de usuário!',
                ],
                 passwordRegra: [
                    v => !!v || 'Informe a palavra-passe do usuário!',
                ],
        }
    },
    methods: {
        ...mapActions({
            registrar:'usuario/registrar'
        }),
        
        async registar(e){
            try {
                this.loading = true
              
                if(!this.form.senha) throw 'informe a sua senha'
                if(this.senhaPreview !== this.form.senha) throw 'Senha não consta o mesmo valor'
                if(!this.form.concorda) throw 'Tens que concordar com o nosso termo'

                   
               await this.registrar(this.form)

              
               this.snackbar = true
               this.loading = false
                
            } catch (error) {
                this.loading = false
                alert(error)
                
            }
            
            
            


       
        }
    },

    
  }
  </script>